.backdrop-team {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.25);
  transition: opacity 600ms linear, visability 250ms linear;
}
.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop-team.is-hidden .modal-team {
  transform: translate(-50%, -50%) perspective(400px) rotateX(60deg);
}
.modal-team {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 280px;
  height: 500px;
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 4px;
  overflow: scroll;
  @media screen and (min-width: 768px) {
    width: 500px;
    height: 760px;
  }
  @media screen and (min-width: 1024px) {
    width: 552px;
    height: 760px;
  }
}
.modal__header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 40px;
  padding-left: 5px;
  padding-right: 5px;
  text-transform: uppercase;
  background-color: $accent-color;
  @media screen and (min-width: 768px) {
    height: 60px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.modal__close-btn {
  width: 30px;
  height: 30px;
}
.modal__close-btn:hover .modal__close-svg,
.modal__close-btn:focus .modal__close-svg {
  fill: #2196f3;
}
.modal__close-svg {
}
.modal__team-tittle {
  text-align: center;
}
.modal__tittle {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.33;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}
.modal__time {
  font-weight: 400;
  font-size: 10px;
  line-height: 1.33;
  @media screen and (min-width: 768px) {
    font-size: 12px;
  }
}
.modal__img-thumb {
  width: 30px;
  height: 30px;
  transition: transform 400ms linear;

  &:hover,
  &:focus {
    transform: rotate(360deg);
  }
  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
  }
  @media screen and (min-width: 1024px) {
    width: 55px;
    height: 55px;
  }
}
.modal__team-img {
  width: 100%;
  height: 100%;
}
.team-list {
  padding: 5px;
  @media screen and (min-width: 768px) {
    padding: 10px;
  }
  @media screen and (min-width: 1024px) {
    padding: 15px;
  }
}
.team-list__item {
  display: flex;
  margin-bottom: 5px;
  &:last-child {
    margin-bottom: 0px;
  }
  &:nth-child(2n) {
    flex-direction: row-reverse;
  }
}
