.team-list__item:hover .team-list__message--send {
  box-shadow: 15px 10px 20px rgba(255, 107, 1, 0.8);
}
.team-list__item:hover .team-list__message--recive {
  box-shadow: -15px 10px 20px rgba(255, 107, 1, 0.8);
}
.team-list__img-thumb {
  display: inline-block;
  width: 60px;
  height: 60px;
  z-index: 3;
  transition: transform 200ms linear;
  &:hover {
    transform: scale(1.2);
  }
  @media screen and (min-width: 768px) {
    width: 80px;
    height: 80px;
  }
  @media screen and (min-width: 1024px) {
    width: 100px;
    height: 100px;
  }
}
.team-list__member-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.team-list__message {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  width: 140px;
  max-height: 55px;
  margin-right: 15px;
  margin-left: 15px;
  padding-top: 3px;
  border-radius: 25px;
  background-color: $accent-color;
  &:before,
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 25px;
  }

  @media screen and (min-width: 768px) {
    width: 160px;
    min-height: 60px;
  }
}
.team-list__message--send:before {
  left: -7px;
  width: 20px;
  background-color: $accent-color;
  border-bottom-right-radius: 16px 14px;
}
.team-list__message--send:after {
  left: -26px;
  width: 26px;
  background-color: rgba(0, 0, 0, 0.9);
  border-bottom-right-radius: 10px;
}
.team-list__message--recive:before {
  right: -7px;
  width: 20px;
  background-color: $accent-color;
  border-bottom-left-radius: 16px 14px;
}

.team-list__message--recive:after {
  right: -26px;
  width: 26px;
  background-color: rgba(0, 0, 0, 0.9);
  border-bottom-left-radius: 10px;
}
.team-list__member-name {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.33;
  margin-bottom: 3px;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}
.team-list__message-content {
  display: flex;
  align-items: flex-end;
}
.team-list__message-content--reverse {
  flex-direction: row-reverse;
}
.team-list__socials {
  display: flex;
  text-align: center;
}
.team-list__social-item {
  margin-right: 13px;
  &:last-child {
    margin-right: 0px;
  }
}
.team-list__link {
  fill: #ffffff;
}
.team-list__link:hover .team-list__link-svg,
.team-list__link:focus .team-list__link-svg {
  fill: #2196f3;
}
.team-list__link-svg {
}
.team-list__message-time {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.33;
}
.team-list__message-time--row {
  margin-left: 13px;
  @media screen and(min-width:768px) {
    margin-left: 20px;
  }
}
.team-list__message-time--reverse {
  margin-right: 13px;
  @media screen and(min-width:768px) {
    margin-right: 20px;
  }
}
